border: none;
color: white;
padding: 10px 28px ;
text-align: center;
font-size: 20px;
border-radius: 10px;
}

[type="file"] {
    border: 0;
    clip: rect(0, 0, 0, 0);
    height: 1px;
    overflow: hidden;
    padding: 0;
    position: absolute !important;
    white-space: nowrap;
    width: 2px;
  }
   
  [type="file"] + label {
    background-color: #000;
    border-radius: 10px;
    color: #fff;
    cursor: pointer;
    display: inline-block;
    padding-left: 2rem 4rem;
  }
    
  [type="file"]:focus + label,
  [type="file"] + label:hover {
      background-color: #feb236;
  }
    
  .error{
    align-items: center;
    display: flex;
    margin-left: 150px;
  }
  .button1 {
    border-radius: 4px;
    width: 300px;
    transition: all 0.5s;
    cursor: pointer;
    margin: 5px;
    background-color: rgb(10, 65, 50); /* Green */
    border: none;
    color: white;
    padding: 10px 28px ;
    text-align: center;
    font-size: 30px;
    margin-top: 40px;
  
  }
  .btn {
    cursor: pointer;
    background-color: green; /* Green */
    border: none;
    color: white;
    padding: 10px 28px ;
    text-align: center;
    font-size: 30px;
    margin-top: 10px;
    border-radius: 10px;
    box-shadow: 0 5px 5px #808888;
  }